@import url(https://fonts.googleapis.com/css2?family=Merriweather&family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  height: 100%;
}
#root {
  min-height: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  padding-bottom: calc(100px + 2rem);
}

body {
  padding-top: 10rem;
  font-family: "Montserrat", sans-serif !important;
  background: black !important;
  /* background: url("./assets/bg1.jpg") no-repeat;
  background-size: cover;
  background-attachment: fixed; */
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Merriweather", serif !important;
}
.website-title {
  -webkit-text-stroke-width: 0.2px;
  -webkit-text-stroke-color: blueviolet;
}
.navbar,
.footer {
  color: #5d4298;
  display: flex;
  height: 100px;
  background-color: #1c1c1c;
  justify-content: center;
  padding: 0 3rem;
}
.bottom-0 {
  bottom: 0 !important;
}
.navbar-nav {
  width: 100%;
  margin: 0 2rem;
}

.nav-link {
  color: black;
}

.search-bar {
  width: auto;
  border-radius: 10rem;
  align-content: center;
  margin-top: 1rem;
}
.search-bar input,
.search-bar input:active,
.search-bar input:focus {
  color: white;
  width: 100%;
  margin-left: 1rem;
  font-size: 1.4rem;
  border-radius: 1rem;
  border: none !important;
  outline: none !important;
  box-shadow: none !important;
  background: linear-gradient(#000, #000) center bottom 5px / calc(100% - 10px)
    2px no-repeat !important;
}

.search-button {
  background: #1c1c1c !important;
  border: none !important;
  font-size: 1.8rem !important;
}
.search-button:hover {
  transition: 0.5s ease all;
  transform: rotateZ(120deg);
}

.middleComponent {
  margin-top: 150px;
}

.quote {
  padding: 1rem;
  margin: 5rem 1rem;
  box-shadow: 0 2px 3px rgba(255, 255, 255, 0.3), 0 -1px 2px rgba(0, 0, 0, 0.2);
  color: white;
}
.anime {
  color: white;
}

.anime-score {
  margin-top: 3rem;
}

.dropdown-button {
  padding: 1rem;
  margin-left: 1rem !important;
}
.dropdown-button .btn-primary {
  background-color: transparent;
  border-color: transparent;
}
.dropdown-button .btn-primary:hover,
.dropdown-button .btn-primary:focus,
.dropdown-button .btn-primary:active {
  background-color: #5d4298 !important;
  border-color: #5d4298 !important;
}
.home-button {
  margin-top: 1rem;
  background-color: transparent !important;
  border-color: transparent !important;
}
.home-button .btn-secondary:focus,
.btn-secondary:hover {
  background-color: #5d4298 !important;
  border-color: #5d4298 !important;
}

.btn-primary,
.btn-secondary {
  font-size: 1.5rem !important;
  background-color: transparent !important;
  border-color: transparent !important;
  box-shadow: none !important;
}

.radioButton {
  margin-top: 1rem;
  background-color: transparent;
  border-color: transparent;
}
.radioButton .btn .active .btn-secondary {
  background-color: #5d4298 !important;
}

.footer {
  margin-top: 2rem !important;
  color: white;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.loader {
  height: calc(100vh - 10rem);
}
/* search button */
#s-cover {
  width: 1px;
  transform: scale(0.4);
}
#s-cover button {
  margin-top: -1rem;
  position: relative;
  display: block;
  width: 84px;
  height: 75px;
  cursor: pointer;
  background: transparent;
  border: none;
}
#s-circle {
  position: relative;
  top: -20px;
  left: 0;
  width: 50px;
  height: 50px;
  margin-top: 0;
  border-width: 15px;
  border: 15px solid #fff;
  background-color: transparent;
  border-radius: 50%;
  transition: 0.5s ease all;
}

#s-cover button span {
  position: absolute;
  top: 45px;
  left: 38px;
  display: block;
  width: 25px;
  height: 15px;
  background-color: transparent;
  border-radius: 10px;
  transform: rotateZ(52deg);
  transition: 0.5s ease all;
}

#s-cover button span:before,
#s-cover button span:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 45px;
  height: 15px;
  background-color: #fff;
  border-radius: 10px;
  transform: rotateZ(0);
  transition: 0.5s ease all;
}

#s-cover:hover #s-circle {
  top: -1px;
  width: 67px;
  height: 15px;
  border-width: 0;
  background-color: #fff;
  border-radius: 20px;
}

#s-cover:hover span {
  top: 50%;
  left: 56px;
  width: 25px;
  margin-top: -9px;
  transform: rotateZ(0);
}

#s-cover:hover button span:before {
  bottom: 11px;
  transform: rotateZ(52deg);
}

#s-cover:hover button span:after {
  bottom: -11px;
  transform: rotateZ(-52deg);
}
#s-cover:hover button span:before,
#s-cover:hover button span:after {
  right: -6px;
  width: 40px;
  background-color: #fff;
}
.hamBurger {
  position: absolute;
  top: 1.5rem;
  left: 0.2rem;
}

@media screen and (max-width: 768px) {
  .bg-nav-dark {
    background: #1c1c1c;
  }
  body {
    font-size: 10px !important;
  }
  .search-bar-input {
    width: calc(100% - 80px) !important;
  }
  .navbar-nav {
    margin: 0;
    max-width: 100vw;
  }
  .nav-wrap {
    justify-content: center !important;
  }
  .website-title {
    font-size: 1.5rem;
    white-space: nowrap;
  }
  .nav-item *,
  .TopListItem h4,
  .TopListItem h3 {
    font-size: 1.2rem !important;
  }
}
.nav-tabs {
  background: #1c1c1c;
  margin-bottom: 2rem !important;
}
.nav-tabs .nav-link.active {
  background-color: #5d4298 !important;
  color: white !important;
}
.nav-link {
  color: white;
}
a:hover {
  color: #5d4298 !important;
}
.breadcrumb {
  background-color: white !important;
}
.download-button {
  background-color: #5d4298;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 15px;
}
.download-button:hover {
  background-color: #6d54a2;
}

.container:hover .Back,
.container:hover .Next {
  opacity: 1;
}
.slide {
  flex: 1 1;
}
.container {
  position: relative;
  padding: 0 !important;
}
.button {
  width: 40px;
  position: absolute;
  top: 51px;
  padding: 1rem 0.4rem;
  font-size: 2rem;
  border: none;
  outline: none !important;
  color: #fff;
  height: calc(100% - 51px);
  background: transparent;
  border-radius: 0.6rem;
}
.Back {
  left: 0;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.8),
    rgba(0, 0, 0, 0)
  );
  opacity: 0;
  transition: transform 0.15s ease-in-out, opacity 0.15s ease-in-out;
  transform: translateX(2.5px);
}

.Next {
  right: 0;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.8)
  );
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  transform: translateX(-2.5px);
}

.card {
  height: 100%;
  position: relative;
  overflow: hidden;
  border: 1px solid #1c1c1c !important;
  border-radius: 0.6rem !important;
  margin: 0 0.2rem;
  cursor: pointer;
}
.card-image {
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 1;
  height: 100%;
  background-size: contain;
  background-position: center;
  transition: transform 0.15s ease-in-out;
  overflow: hidden;
}
.card:hover .card-image {
  transform: scale(1.1);
}
.card-image::before {
  content: "";
  position: absolute;
  background: inherit;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* box-shadow: inset 0 0 2000px rgba(255, 255, 255, 0.5); */

  margin: -20px;
}
.card:hover .card-image::before {
  -webkit-filter: blur(0px);
          filter: blur(0px);
}
.card-title {
  padding: 16px 2rem;
  position: absolute;
  color: #fff;
  font-weight: bold;
  /* background: radial-gradient(center, #00000000 0%, #000000 80%); */
  background: radial-gradient(circle at 5% 5%, #00000000 0%, #000000 90%);
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  /* pointer-events: none; */
  z-index: 2;
}

.card-title .anime-title {
  flex: 0 0 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-height: 80px;
  width: 100%;
}
.episode,
.score {
  position: absolute;
  top: 0.5rem;
  background: black;
  color: white;
  padding: 0.4rem 1rem;
  border-radius: 1em;
}

.score {
  right: 0.5rem;
}
.episode {
  left: 0.5rem;
}

.TopList {
  padding: 1rem;

  list-style: none;
  display: flex;
  flex-direction: column;
  border: 5px solid #1c1c1c;
  border-radius: 0.5rem;
  overflow: hidden;
}

.TopListItem {
  display: flex;
  align-items: center;
  height: 140px;
  padding: 1rem 2rem;
  line-height: 1;
  color: whitesmoke;
  transition: 0.5s;
}
.TopListItem:hover {
  color: white;
}

.TopListItem *:not(:last-child) {
  margin-right: 1rem;
}

.TopListItem img {
  border: 1px solid #1c1c1c;
  max-height: 100px;
  max-width: 80px;
  min-width: 80px;
}
.rank {
  padding: 0.5rem;
  border: 2px solid white;
  border-radius: 0.5rem;
  margin-right: 1.5rem !important;
  font-size: 1.5rem;
}
.rank:hover {
  border: 2px solid #6e80f3;
}
@media screen and (max-width: 768px) {
  .rank {
    padding: 0.4rem;
    border: 2px solid white;
    border-radius: 0.5rem;
    margin-right: 1rem !important;
    font-size: 1.5rem;
  }
  .TopList {
    padding: 0.1rem;
    overflow-x: scroll;
  }
}

